.days {
  width: 97%;
  white-space: nowrap;
  background: rgba(16, 33, 54, .8);
  border-bottom-left-radius: 30px;
  border-bottom-right-radius: 30px;
  flex-direction: row;
  justify-content: space-between;
  margin: 321px auto;
  padding: 15px 118px;
  display: none;
  overflow-x: auto;
  box-shadow: 0 8px 43px rgba(16, 33, 54, .6);
}

.weather-card {
  width: 152px;
  height: 217px;
  border: 1px solid rgba(255, 255, 255, .2);
  border-radius: 25px;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  margin-top: 33px;
  margin-bottom: 40px;
  display: flex;
}

.weather-card:nth-child(n+9) {
  display: none;
}

.weather-card:hover, .weather-card:focus {
  background: rgba(255, 255, 255, .1);
}

.weather-card__time-hour {
  color: #fff;
  opacity: .54;
  text-align: center;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.weather-card__time-temp {
  color: #ff6b09;
  text-align: center;
  font-size: 25px;
  font-weight: 700;
}

.weather-card__time-icon {
  width: 35px;
  height: 35px;
}

.weather-card__barometer, .weather-card__humidity, .weather-card__wind {
  align-items: center;
  display: flex;
}

.weather-card__details {
  flex-direction: column;
  gap: 4px;
  display: flex;
}

.weather-card__details-text {
  color: #fff;
  opacity: .54;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.weather-card__details-icons {
  width: 20px;
  height: 20px;
  margin-right: 10px;
}

